@import url("https://fonts.googleapis.com/css2?family=Neucha&display=swap");

.App {
  width: 100vw;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #7fb2e7;
  font-family: "Neucha", cursive !important;
}

.app-heading {
  text-transform: uppercase;
  font-size: 40px;
  margin: 30px 0;
  color: white;
  z-index: 1;
  text-align: center;
}
.filter-label{
  text-align: center;
}

@media (max-width: 800px) {
  .heading {
    margin: 15px 0;
    font-size: 35px;
  }

  @media (max-width : 425px){
   .radio-class{ 
    width: 80%;
    margin: auto;
    align-items: center;
    justify-content:center;
  }
  }
}